

import { User, Tokens } from "../types";

/* -------------------------------------------------------------------------- */
/* Configuraci√≥n                                                              */
/* -------------------------------------------------------------------------- */

const API_BASE_URL = "/api/8083/user-service";

/** Devuelve el header Authorization si hay token almacenado. */
const authHeader = (): HeadersInit => {
    const token = localStorage.getItem("accessToken");
    return token ? { Authorization: `Bearer ${token}` } : {};
};

/* -------------------------------------------------------------------------- */
/* 1.  Auth: register / login / refresh                                       */
/* -------------------------------------------------------------------------- */

type RegisterPayload = {
    username: string;
    email: string;
    password: string;
};
type LoginPayload = {
    email: string;
    password: string;
};
type RefreshPayload = { refreshToken: string };

/** POST /auth/register ‚Äî crea cuenta y devuelve usuario + tokens. */
export async function register(
    data: RegisterPayload
): Promise<{ user: User; tokens: Tokens }> {
    const res = await fetch(`${API_BASE_URL}/auth/register`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(data),
    });
    if (!res.ok) throw new Error("No se pudo registrar al usuario");
    const json = await res.json();

    // Guarda tokens para futuras peticiones
    localStorage.setItem("accessToken", json.tokens.accessToken);
    localStorage.setItem("refreshToken", json.tokens.refreshToken);

    return json;
}

/** POST /auth/login ‚Äî valida credenciales y devuelve tokens. */
export async function login(data: LoginPayload): Promise<Tokens> {
    const res = await fetch(`${API_BASE_URL}/auth/login`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(data),
    });
    if (!res.ok) throw new Error("Credenciales inv√°lidas");
    const tokens: Tokens = await res.json().then((j) => j.tokens);

    localStorage.setItem("accessToken", tokens.accessToken);
    localStorage.setItem("refreshToken", tokens.refreshToken);

    return tokens;
}

/** POST /auth ‚Äî renueva accessToken usando refreshToken. */
export async function refresh(data: RefreshPayload): Promise<Tokens> {
    const res = await fetch(`${API_BASE_URL}/auth`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(data),
    });
    if (!res.ok) throw new Error("Refresh token inv√°lido");
    const tokens: Tokens = await res.json().then((j) => j.tokens);

    localStorage.setItem("accessToken", tokens.accessToken);
    localStorage.setItem("refreshToken", tokens.refreshToken);

    return tokens;
}

/* -------------------------------------------------------------------------- */
/* 2.  CRUD de usuarios (rutas protegidas, requieren JWT)                     */
/* -------------------------------------------------------------------------- */

/** GET /users ‚Äî lista todos los usuarios. */
export async function getUsers(): Promise<{ users: User[] }> {
    const res = await fetch(`${API_BASE_URL}/users`, {
        headers: authHeader(),
    });
    if (res.status === 401) throw new Error("No autorizado");
    if (!res.ok) throw new Error("No se pudieron obtener los usuarios");
    return res.json();
}

/** GET /users/:id ‚Äî obtiene un usuario por ID. */
export async function getUserById(id: number): Promise<User> {
    const res = await fetch(`${API_BASE_URL}/users/${id}`, {
        headers: authHeader(),
    });
    if (res.status === 401) throw new Error("No autorizado");
    if (!res.ok) throw new Error(`Usuario ${id} no encontrado`);
    return res.json().then((j) => j.user);
}

/** POST /users ‚Äî crea un usuario (requiere token con rol adecuado). */
export async function createUser(user: Omit<User, "id">): Promise<User> {
    const res = await fetch(`${API_BASE_URL}/users`, {
        method: "POST",
        headers: { "Content-Type": "application/json", ...authHeader() },
        body: JSON.stringify(user),
    });
    if (res.status === 401) throw new Error("No autorizado");
    if (!res.ok) throw new Error("Error al crear el usuario");
    return res.json().then((j) => j.user);
}

/** PUT /users/:id ‚Äî actualiza un usuario existente. */
export async function updateUser(
    id: number,
    user: Partial<User>
): Promise<User> {
    const res = await fetch(`${API_BASE_URL}/users/${id}`, {
        method: "PUT",
        headers: { "Content-Type": "application/json", ...authHeader() },
        body: JSON.stringify(user),
    });
    if (res.status === 401) throw new Error("No autorizado");
    if (!res.ok) throw new Error("Error al actualizar el usuario");
    return res.json().then((j) => j.user);
}

/** DELETE /users/:id ‚Äî elimina un usuario por ID. */
export async function deleteUser(id: number): Promise<void> {
    const res = await fetch(`${API_BASE_URL}/users/${id}`, {
        method: "DELETE",
        headers: authHeader(),
    });
    if (res.status === 401) throw new Error("No autorizado");
    if (!res.ok) throw new Error("Error al eliminar el usuario");
}
