"use client";

import { useState, useEffect, type ChangeEvent, type FormEvent } from "react";
import { useRouter } from "next/navigation";
import Image from "next/image";

import { Card, CardContent } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Button } from "@/components/ui/button";

import { useAuth } from "@/context/AuthContext";
import { login as loginService, getUserById } from "@/lib/services/userService";
import type { User, Tokens } from "@/lib/types";

/* -------------------------------------------------------------------------- */
/* Helper para decodificar (sin verificar) el payload de un JWT               */
/* -------------------------------------------------------------------------- */
const decodeJWT = <T,>(token: string): T => {
    const payload = token.split(".")[1];
    const json = atob(payload.replace(/-/g, "+").replace(/_/g, "/"));
    return JSON.parse(json) as T;
};

interface JWTClaims {
    sub: string; // userID
    username: string;
    exp: number;
    iat: number;
}

/* -------------------------------------------------------------------------- */
/* Tipos locales                                                               */
/* -------------------------------------------------------------------------- */
interface FormState {
    identifier: string;
    password: string;
}

/* -------------------------------------------------------------------------- */
/* Componente principal                                                        */
/* -------------------------------------------------------------------------- */
export default function LoginForm() {
    const { login, isAuthenticated } = useAuth();
    const router = useRouter();

    const [form, setForm] = useState<FormState>({
        identifier: "",
        password: "",
    });
    const [error, setError] = useState("");
    const [success, setSuccess] = useState("");

    /* --------------------------- Manejadores --------------------------- */

    const handleChange = (e: ChangeEvent<HTMLInputElement>) => {
        const { name, value } = e.target;
        setForm((prev) => ({ ...prev, [name]: value }));
    };

    const handleSubmit = async (e: FormEvent) => {
        e.preventDefault();
        setError("");
        setSuccess("");

        const { identifier, password } = form;

        // Validaci√≥n r√°pida
        if (!identifier.trim() || password.length < 8) {
            setError(
                "Ingresa un correo v√°lido y contrase√±a de al menos 8 caracteres."
            );
            return;
        }

        try {
            /* 1. Llamar al backend */
            const tokens: Tokens = await loginService({
                email: identifier,
                password,
            });

            /* 2. Extraer userID y username del access‚Äëtoken */
            const claims = decodeJWT<JWTClaims>(tokens.accessToken);
            const userId = Number(claims.sub);

            /* 3. Obtener datos completos del usuario (opcional) */
            // Si tienes endpoint /users/:id protegido, √∫salo:
            let user: User;
            try {
                user = await getUserById(userId);
            } catch {
                // fallback m√≠nimo: usa lo que tengas del JWT
                user = {
                    id: userId,
                    username: claims.username,
                    email: identifier,
                    subscribedNewsletter: false,
                    createdAt: "",
                    updatedAt: "",
                };
            }

            /* 4. Guardar en AuthContext */
            login(user, tokens);

            /* 5. Feedback */
            setSuccess(`¬°Bienvenido, ${user.username}!`);
            setForm({ identifier: "", password: "" });
        } catch (err: any) {
            console.error(err);
            setError(err?.message || "Credenciales inv√°lidas");
        }
    };

    /* ----------------------- Redirecci√≥n post‚Äëlogin -------------------- */
    useEffect(() => {
        if (isAuthenticated) {
            const t = setTimeout(() => router.push("/"), 700);
            return () => clearTimeout(t);
        }
    }, [isAuthenticated, router]);

    /* --------------------------- UI ------------------------------------ */
    return (
        <div className="flex flex-col gap-6">
            <Card className="overflow-hidden p-0">
                <CardContent className="grid p-0 md:grid-cols-2">
                    {/* ---------- Formulario ---------- */}
                    <form onSubmit={handleSubmit} className="p-6 md:p-8">
                        <div className="flex flex-col gap-6">
                            {/* Encabezado */}
                            <div className="flex flex-col items-center text-center">
                                <h1 className="text-2xl font-bold text-yellow-600">
                                    ¬°Bienvenido de nuevo al Kwik‚ÄëE‚ÄëMart!
                                </h1>
                                <p className="text-sm text-muted-foreground">
                                    Inicia sesi√≥n para llenar tu carrito con
                                    ofertas de Springfield üòé
                                </p>
                            </div>

                            {/* Email */}
                            <div className="grid gap-2">
                                <Label htmlFor="identifier">
                                    Correo electr√≥nico
                                </Label>
                                <Input
                                    id="identifier"
                                    name="identifier"
                                    type="email"
                                    placeholder="apu@kwikemart.com"
                                    value={form.identifier}
                                    onChange={handleChange}
                                    required
                                />
                            </div>

                            {/* Password */}
                            <div className="grid gap-2">
                                <div className="flex items-center">
                                    <Label htmlFor="password">Contrase√±a</Label>
                                    <a
                                        href="#"
                                        className="ml-auto text-sm underline-offset-2 hover:underline">
                                        ¬øOlvidaste tu contrase√±a?
                                    </a>
                                </div>
                                <Input
                                    id="password"
                                    name="password"
                                    type="password"
                                    placeholder="m√≠nimo 8 caracteres"
                                    value={form.password}
                                    onChange={handleChange}
                                    required
                                    minLength={8}
                                />
                            </div>

                            {/* Bot√≥n login */}
                            <Button
                                type="submit"
                                className="w-full bg-yellow-500 hover:bg-yellow-600">
                                Iniciar sesi√≥n
                            </Button>

                            {/* Enlace a registro */}
                            <div className="text-center text-sm">
                                ¬øNo tienes cuenta?{" "}
                                <a
                                    href="/registro"
                                    className="underline underline-offset-4">
                                    Reg√≠strate
                                </a>
                            </div>

                            {/* Estado */}
                            {error && (
                                <p className="text-sm font-medium text-red-600">
                                    {error}
                                </p>
                            )}
                            {success && (
                                <p className="text-sm font-medium text-green-600">
                                    {success}
                                </p>
                            )}
                        </div>
                    </form>

                    {/* ---------- Imagen lateral ---------- */}
                    <div className="relative hidden bg-muted md:block">
                        <Image
                            src="/images/banner2.jpg"
                            alt="Interior del Kwik‚ÄëE‚ÄëMart"
                            className="absolute inset-0 h-full w-full object-cover brightness-90"
                            fill
                            priority
                        />
                    </div>
                </CardContent>
            </Card>

            {/* ---------- T√©rminos ---------- */}
            <div className="text-center text-xs dark:text-muted-foreground *:[a]:underline *:[a]:underline-offset-4">
                Al hacer clic en ‚ÄúIniciar sesi√≥n‚Äù, aceptas nuestros{" "}
                <a href="#">T√©rminos del Kwik‚ÄëE‚ÄëMart</a> y{" "}
                <a href="/politica">Pol√≠tica de Privacidad</a>.
            </div>
        </div>
    );
}
