// components/ui/label.tsx
// -----------------------------------------------------------------------------
//
//  ‚Ä¢ Expone el slot `data-slot="label"`  ‚Üí  permite seleccionar el componente
//    f√°cilmente desde CSS/Tailwind (ej. `[data-slot=label] {...}`).
//  ‚Ä¢ A√±ade un conjunto de utilidades Tailwind para tipograf√≠a y disabled-state.
//  ‚Ä¢ Acepta `className` para sobrescribir o extender estilos.
//
// -----------------------------------------------------------------------------

"use client"

import * as React from "react"
import * as LabelPrimitive from "@radix-ui/react-label"

import { cn } from "@/lib/utils"

/**
 * Label
 * ---------------------------------------------------------------------------
 * @param className  
 * @param props      
 */
function Label({
  className,
  ...props
}: React.ComponentProps<typeof LabelPrimitive.Root>) {
  return (
    <LabelPrimitive.Root
      /* Slot para theming con Tailwind */
      data-slot="label"
      /* Estilos base + los que lleguen por props */
      className={cn(
        // ‚Äî Layout
        "flex items-center gap-2",
        // ‚Äî Tipograf√≠a
        "text-sm font-medium leading-none select-none",
        // ‚Äî Estados disabled (dos variantes: group-data & peer-disabled)
        "group-data-[disabled=true]:pointer-events-none group-data-[disabled=true]:opacity-50",
        "peer-disabled:cursor-not-allowed peer-disabled:opacity-50",
        
        className
      )}
      {...props}
    />
  )
}

export { Label }
